// React
import React, { createContext, useState } from "react";

// 
interface TitleContextType {
    title: string; // 
    setTitle: (newTitle: string) => void; // 
}

// 
export const TitleContext = createContext<TitleContextType>({
    title: "", // 
    setTitle: () => {}, // 
});

// 
export const TitleContextProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
    // useState Hook
    // useState Hook
    const [title, setTitle] = useState<string>(""); // setTitleRaw

    // setTitle
    // const setTitle = (newTitle: string) => {
    //     console.log("setTitle function executed with:", newTitle);
    //     setTitleRaw(newTitle);
    // };
    // 
    const contextValue = {
        title, // 
        setTitle, // 
    };

    // TitleContext.ProvidertitlesetTitle
    return (
        <TitleContext.Provider value={contextValue}>
            {children} {/*  */}
        </TitleContext.Provider>
    );
};